

export const goTo = (url = "") => {
  url = window.location.origin + "/" + url;
  window.location.replace(url);
}

export const convertToPercent = (numberator, donominator) => {

  const percent = (numberator / donominator) * 100;

  const result = 100 - percent

  return result;

}

export const checkStatus = (type) => {
  switch (type) {
    case 'book':
      return 'Cha thanh ton'
    case 'payment':
      return ' thanh ton'
    case 'done':
      return ' tr phng'
    case 'cancel':
      return ' hy phng'
    default:
      break;
  }
}


export const checkQualityRoom = (type) => {
  switch (type) {
    case 'basic':
      return "Phng bnh dn";
    case 'medium':
      return "Phng tiu chun";
    case 'good':
      return "Phng cht lng cao";
    case 'luxury':
      return "Phng hng sang";
    default:
      break;
  }
}

export const checkTypeRoom = (type) => {
  switch (type) {
    case 'single':
      return "Ging n";
    case 'couple':
      return "Ging i";
    case 'king':
      return "Ging n king";
    case 'queen':
      return "Ging n queen";
    default:
      break;
  }
}