import React from "react";
import { Form as FormRender, Input, Button, Label, Row, Col } from "reactstrap";
import Form from "../Form/Form";
import OrderService from "../../Services/OrderService"
import { checkQualityRoom, checkStatus, checkTypeRoom } from "../../../Constances/Util";
import { Divider } from "@mui/material";
import { reformatDate } from "../../../Constances/const";

class PrintForm extends Form {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    checkDay = (day) => {
        switch (day) {
            case 0:
                return "Ch nht";
            case 1:
                return "Th hai";
            case 2:
                return "Th ba";
            case 3:
                return "Th t";
            case 4:
                return "Th nm";
            case 5:
                return "Th sau";
            case 6:
                return "Th by";
            default:
                break;
        }
    }



    render() {

        const data = this.props.data;

        const customer = data?.customer

        const item = data?.items ? data?.items[0] : {};

        const room = item?.room

        // const 

        const current = new Date()

        var current_day = current.getDay();
        return (
            <div className="PrintForm" id="PrintForm">
                <Row>
                    <Col xs='6' className="footer-print">
                        <Row className="footer-row name-hotel">
                            <h4>Khch sn Phng ng</h4>
                        </Row>
                        <Row className="footer-row">
                            <i><b>Add: 97 Phan Chu Trinh,  Nng</b></i>
                        </Row>
                        <Row className="footer-row">
                            <i><b>Tell:05113.821.266/ 550.666</b></i>
                        </Row>
                        <Row className="footer-row">
                            <i><b>Email: otc@phuongdong.com.vn</b></i>
                        </Row>
                    </Col>
                </Row>
                <Row className="title-print text-center"><b><h4>Ha n thanh ton dch v</h4></b></Row>
                <Row className="d-flex justify-content-around">
                    <Col sm='5'>
                        {`${this.checkDay(current_day)}, ${current.getDate()}/${current.getMonth() + 1}/${current.getFullYear()}`}
                    </Col>
                    <Col sm='5' className="d-flex justify-content-end">
                        <b>M ha n: {data ? data.id : '12345678'}</b>
                    </Col>
                </Row>
                <Row className="row-divider">
                    <Divider className="divider-print"></Divider>
                </Row>
                <Row className="info-print">
                    <Col xs='6' >
                        <Row >
                            <Col sm='5'>Tn</Col>
                            <Col sm='7'>: <b>{customer?.name}</b></Col>
                        </Row>
                        <Row >
                            <Col sm='5'>a ch</Col>
                            <Col sm='7'>:</Col>
                        </Row>
                        <Row >
                            <Col sm='5'>in thoi</Col>
                            <Col sm='7'>: <b>{customer?.phone}</b></Col>
                        </Row>
                        <Row >
                            <Col sm='5'>M s thu</Col>
                            <Col sm='7'>:</Col>
                        </Row>
                        <Row >
                            <Col sm='5'>Phng</Col>
                            <Col sm='7'>: <b>{room?.name}</b></Col>
                        </Row>
                    </Col>
                    <Col xs='6' >
                        <Row >
                            <Col sm='5'>Ngy n</Col>
                            <Col sm='7'>: <b>{item? reformatDate(item?.start) : ''}</b> 10:30 sng</Col>
                        </Row>
                        <Row>
                            <Col sm='5'>Ngy i</Col>
                            <Col sm='7'>: <b>{item? reformatDate(item?.end): ''}</b> 10:30 sng</Col>
                        </Row>
                        <Row >
                            <Col sm='5'>S ngy</Col>
                            <Col sm='7'>: <b>{item?.amount}</b></Col>
                        </Row>
                        <Row >
                            <Col sm='5'>Thu ngn</Col>
                            <Col sm='7'>:</Col>
                        </Row>
                        <Row>
                            <Col sm='5'>Thanh ton</Col>
                            <Col sm='7'>: Thu ngn</Col>
                        </Row>
                    </Col>
                </Row>

                <Row className="row-divider">
                    <Divider className="divider-print"></Divider>
                </Row>
                <Row className="total-print d-flex justify-content-around">
                    <Col xs='4' >
                        <Row >
                            <b>Ngy</b>
                        </Row>
                        <Row >
                        <b>{`${current.getDate()}/${current.getMonth() + 1}/${current.getFullYear()}`}</b>
                        </Row>
                    </Col>
                    <Col xs="6">
                        <Row>
                            <Col xs='6' >
                                <Row >
                                    <b>Chi tit</b>
                                </Row>
                                <Row >
                                    Tin phng
                                </Row>
                            </Col>
                            <Col xs='6' className="text-center">
                                <Row >
                                    <b>S tin</b>
                                </Row>
                                <Row >
                                    <span>{item.total}</span>
                                </Row>
                            </Col>
                        </Row>
                        <Row>
                            <Divider className="divider-print"></Divider>
                        </Row>
                    </Col>
                </Row>
                <Row className="total-print d-flex justify-content-around">
                    <Col xs='4' className="d-flex justify-content-center">
                      <i> bao gm thu VAT</i>
                    </Col>
                    <Col xs="6">
                        <Row>
                            <Col sm='6' className=" d-flex justify-content-end"><b>Tng Tin:</b></Col>
                            <Col sm='6' className=" d-flex justify-content-center"><b>{item.total}</b></Col>
                        </Row>
                    </Col>
                </Row>
                <Row className="d-flex justify-content-around">
                    <Col sm='3'>
                        <b>Thu ngn</b>
                    </Col>
                    <Col sm='3'>
                        <b>Khch</b>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default PrintForm;
