import { Row } from "reactstrap";
import { goTo } from "../../../../Constances/Util";
import { ButtonForAll } from "../../../../Shared/Components/Button/ButtonForAll";
const Banner = () => {
    const goToRoomList = () => {
        goTo('app/booking')
    }
    const goToNews = () => {
        goTo('app/news')
    }
    return (
        <>
            <Row className="content">
                <h1>BOOKING AND RELAX</h1>
                <p>Nng tm tri nghim cho chuyn i ca bn! <br/>Tham kho thm nh!</p>
                <Row className="row-btn-banner">
                    <ButtonForAll title="XEM PHNG" functionCallBack={goToRoomList} />
                    <ButtonForAll title="TIN TC"  functionCallBack={goToNews}/>
                </Row>
            </Row>
        </>
    )
}

export default Banner