import React, { useState, useEffect } from "react";
import { Controller, useForm } from "react-hook-form";
import { Row, Col, Input } from 'reactstrap';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Button, FormControlLabel, Radio, RadioGroup, Typography } from "@mui/material";
import { ModalNoti } from "../../../../Shared";
import { goTo } from "../../../../Constances/Util";
import ArrowBackIosRoundedIcon from '@mui/icons-material/ArrowBackIosRounded';
import RoomService from '../../shared/Service';

const schema = yup
    .object({
        name: yup
            .string()
            .min(2, "Ti thiu 2 k t")
            .max(50, "Ti a 50 k t")
            .required("Tn khng c b trng")
        ,
        origin_price: yup
            .number()
            .required("Gi gc khng c b trng")
        ,
        price: yup
            .number()
            .required("Gi phng khng c b trng")

        ,
        quality: yup.string()
            .required("Cht lng khng c b trng")
        ,
        type: yup.string()
            .required("Loi phng khng c b trng"),

        image: yup.string()
            .required("Hnh nh khng c b trng"),

        amount: yup.number()
            .required("S lng khng c b trng"),

        description: yup.string()
            .required("M t khng c b trng")

    })
    .required();

const CreateUpdateRoom = (props) => {

    const {
        register,
        reset,
        handleSubmit,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
    });

    const [dataRoom, setDataRoom] = useState({})
    const [message, setMessage] = useState('');
    const [signUpStatus, setStatus] = useState(false);
    const [statusUpdate, setStatusUpdate] = useState(false);
    const [checkUpdate, setCheckUpdate] = useState(false);

    useEffect(() => {
        const id = props.match.params.id
        if (id) {
            setStatusUpdate(true);
            RoomService.Get_Room(id).then(res => {
                setDataRoom(res.data)
                const data = res.data
                reset({
                    name: data.name,
                    origin_price: data.origin_price,
                    price: data.price,
                    quality: data.quality,
                    type: data.type,
                    status: data.status,
                    image: data.image,
                    description: data.description ?data.description :"",
                    amount:data.amount ? data.amount: 1
                })

            }).catch(err => console.log(err))
        }
    }, [])

    const submitClick = (data) => {
        setCheckUpdate(!checkUpdate);

        if (!statusUpdate) {
            const dataRooms = {
                name: data.name,
                origin_price: data.origin_price,
                price: data.price,
                quality: data.quality,
                type: data.type,
                status: data.status,
                image: data.image,
                description: data.description,
                amount:data.amount
            }
            RoomService.Create_Room(dataRooms).then(res => {
                setStatus(true)
                setMessage(`To phng thnh cng`)
            }).catch(err => {
                console.log(err);
                setMessage('Tn phng  tn ti!!!')
            })
        }
        else {
            const param = {
                name: data.name,
                price: data.price,
                quality: data.quality,
                type: data.type,
                status: data.status,
                image: data.image,
                description:data.description
            }
            RoomService.Update_Room(dataRoom.id, param).then(res => {
                setCheckUpdate(!checkUpdate);
                setStatus(true)
                setMessage(`Cp nht thng tin thnh cng`)
            }).catch(err => {
                console.log(err);
                setMessage('C li xy ra!!!')
            })
        }
    }

    const checkMessageNoti = () => {
        setMessage('')
        if (signUpStatus) {
            goTo('admin/room')
        }
    }

    return (
        <Row className="CustomerView">
            <Typography sx={{
                cursor: 'pointer',
                color: 'blue',
                ':hover': {
                    textDecorationLine: 'underline'
                }
            }} onClick={() => goTo('admin/room')}>   <ArrowBackIosRoundedIcon ></ArrowBackIosRoundedIcon> Back</Typography>
            <Col xs={{ size: 6, offset: 3 }} className='form-container'>
                <form onSubmit={handleSubmit(submitClick)}>
                    <Row>
                        <Col>
                            <Controller
                                control={control}
                                name="name"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>Tn phng</h6>
                                        <Input
                                            className="form-control"
                                            inputRef={ref}
                                            placeholder="Tn phng"
                                            {...field}
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>
                    {
                        !statusUpdate ? <Row>
                            <Col>
                                <Controller
                                    control={control}
                                    name="origin_price"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <h6>Gi gc</h6>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                {...field}
                                                defaultValue=''
                                                placeholder="gi gc"
                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                        </Row>
                            : null
                    }
                    <Row >
                        <Col >
                            <Controller
                                control={control}
                                name="price"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>Gi phng</h6>
                                        <Input
                                            className="form-control"
                                            inputRef={ref}
                                            {...field}
                                            placeholder="Gi phng"
                                            type='string'
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>
                    <Row >
                        <Col >
                            <Controller
                                control={control}
                                name="amount"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>S lng</h6>
                                        <Input
                                            className="form-control"
                                            
                                            inputRef={ref}
                                            {...field}
                                            placeholder="S lng"
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>
                    <Row >
                        <h6>Cht lng</h6>
                        <Col >
                            <Controller
                                control={control}
                                defaultValue={dataRoom.quality ? dataRoom.quality : "basic"}
                                name="quality"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <RadioGroup row aria-label="role" {...field}>
                                        <FormControlLabel
                                            value="basic"
                                            control={<Radio />}
                                            label="Phng bnh dn"
                                        />
                                        <FormControlLabel
                                            value="medium"
                                            control={<Radio />}
                                            label="Phng tiu chun"
                                        />
                                        <FormControlLabel
                                            value="good"
                                            control={<Radio />}
                                            label="Phng cht lng cao"
                                        />
                                        <FormControlLabel
                                            value="luxury"
                                            control={<Radio />}
                                            label="Phng hng sang"
                                        />
                                    </RadioGroup>
                                )}
                            />
                        </Col>
                    </Row>
                    <Row >
                        <h6>Loi phng</h6>
                        <Col >
                            <Controller
                                control={control}
                                defaultValue={dataRoom.type ? dataRoom.type : "single"}
                                name="type"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <RadioGroup row aria-label="role" {...field}>
                                        <FormControlLabel
                                            value="single"
                                            control={<Radio />}
                                            label="Phng n "
                                        />
                                        <FormControlLabel
                                            value="couple"
                                            control={<Radio />}
                                            label="Phng i"
                                        />
                                    </RadioGroup>
                                )}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Controller
                                control={control}
                                name="description"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>M t</h6>
                                        <Input
                                            className="form-control"
                                            inputRef={ref}
                                            placeholder="M t"
                                            {...field}
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Controller
                                control={control}
                                name="image"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>Link nh</h6>
                                        
                                            <Input
                                                inputRef={ref}
                                                placeholder="Link nh"
                                                {...field}
                                            /> 
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>

                    <Row className='d-flex justify-content-center'>
                        <Col className='d-flex justify-content-center' xs='4'>
                            <Button className='btn-signin' variant="contained" type='submit' >{!statusUpdate ? 'To phng' : 'Cp nht'}</Button>
                        </Col>
                    </Row>
                </form>
            </Col>
            <ModalNoti
                message={message}
                done={() => (checkMessageNoti())}
            ></ModalNoti>
        </Row>
    )
}

export default CreateUpdateRoom;