import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import ArrowBackIosRoundedIcon from '@mui/icons-material/ArrowBackIosRounded';
import * as React from 'react';
import { Button } from '@mui/material';
import { Col, Row } from 'reactstrap';
import { checkQualityRoom } from '../../../../Constances/Util';
import { reformatDate } from '../../../../Constances/const';

const style = {
  borderRadius: "10px",
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '700px',
  height: '630px',
  bgcolor: 'background.paper',
  border: '0.3px solid #000',
  boxShadow: 24,
  p: 4,
};

const CheckQualityRoom = (role) => {
  switch (role) {
    case 'basic':
      return 'Phng bnh dn';
      break;

    case 'medium':
      return 'Phng tiu chun';
      break;

    case 'good':
      return 'Phng cht lng cao';
      break;

    case 'luxury':
      return 'Phng hng sang';
      break;

    default:
      break;
  }
}

const checkStatus = (status) => {
  switch (status) {
    case 'book':
      return 'Ch thanh ton';
    case 'payment':
      return ' thanh ton';
    case 'done':
      return ' tr phng';
    case 'cancel':
      return ' hy';
    default:
      break;
  }
}

export default function ModalDetail(props) {
  const { infor, isOpen, detailData, CloseModal, PaymentOrder, updateOrder, cancelOrder, doneOrder,rePrintForm  } = props;

  return (
    <div>
      <Modal open={isOpen} onClose={CloseModal} style={{ zIndex: 900 }}>
        <Box sx={style} className="box-detail-order">
          <Row className='row row-heaher'>
            <Col xs="2" className='order-deteil-col col-back '>
              <Typography sx={{
                cursor: 'pointer',
                color: 'blue',
                ':hover': {
                  textDecorationLine: 'underline'
                }
              }} onClick={CloseModal}>   <ArrowBackIosRoundedIcon ></ArrowBackIosRoundedIcon> Back</Typography>
            </Col>
            <Col className='order-deteil-col col-title'>
              <Typography className='title-header' variant="h6" component="h2">
                Chi tit n hng ca : <strong>{infor}</strong>
              </Typography>
            </Col>
          </Row>
          <Typography className='body-detail' sx={{ mt: 2 }}>
            <Row className='row row-body'>
              <Col className="order-deteil-col">
                <strong>M n hng : </strong><p> {detailData.code}</p>
              </Col>
              <Col className="order-deteil-col">
                <strong>Trng thi : </strong>
                <p>
                  {
                    checkStatus(detailData.status)
                  }
                </p>
              </Col>
            </Row>

            <Row className='row row-body'>
              <Col className="order-deteil-col ">
                <strong>Tn khch hng : </strong><p> {detailData.customer ? detailData.customer.name : ''}</p>
              </Col>
              <Col className="order-deteil-col">
                <strong>Ngy sinh : </strong><p> {detailData.customer ? reformatDate(detailData.customer.birthday) : ''}</p>
              </Col>
            </Row>

            <Row className='row row-body'>
              <Col className="order-deteil-col">
                <strong>S in thoi : </strong><p> {detailData.customer ? detailData.customer.phone : ""}</p>
              </Col>
              <Col className="order-deteil-col">
                <strong>CCCD : </strong><p> {detailData.customer ? detailData.customer.cccd : ""}</p>
              </Col>
            </Row>

            <Row className='row row-body'>
              <Col className="order-deteil-col" >
                <strong>Tn phng : </strong><p> {detailData.items ? detailData.items[0].room.name : ''}</p>
              </Col>
              <Col className="order-deteil-col">
                <strong>Gi phng: </strong><p> {detailData.items ? detailData.items[0].room.price : ''} vn</p>
              </Col>
            </Row>

            <Row className='row row-body'>
              <Col className="order-deteil-col">
                <strong> Cht lng : </strong><p> {detailData.items ? checkQualityRoom(detailData.items[0].room.quality) : ''}</p>
              </Col>
              <Col className="order-deteil-col">
                <strong>Loi phng : </strong>
                <p>
                  {
                    detailData.items ?
                      detailData.items[0].room.type === "single" ? "Phng n" : "Phng i"
                      : ""
                  }
                </p>
              </Col>
            </Row>

            <Row className='row row-body'>
              <Col className="order-deteil-col">
                <strong>T : </strong><p> {detailData.items ? reformatDate(detailData.items[0].start) : ''}</p>
              </Col>
              <Col className="order-deteil-col">
                <strong>n : </strong> <p> {detailData.items ? reformatDate(detailData.items[0].end) : ''}</p>
              </Col>
            </Row>

            <Row className='row row-body'>
              <Col className="order-deteil-col">
                <strong>S lng : </strong><p> {detailData.items ? detailData.items[0].amount : ''}</p>
              </Col>
              <Col className="order-deteil-col">
                <strong>Tng: </strong><p> {detailData.total} vn</p>
              </Col>
            </Row>

            <Row className='row row-body'>
              <Col className="order-deteil-col">
                <strong>Hnh nh : </strong>
                <img
                  className='room-image'
                  style={{ maxHeight: "120px", maxWidth: "200px", margin: "10px" }}
                  src={detailData.items ? detailData.items[0].room.image : ''}
                  alt='text'
                />
              </Col>
            </Row>

          </Typography>
          <Typography className='footer-detail-order'>
            {
              detailData.status === "book" ?
                <>
                  <Button style={{ float: "right", marginLeft: "10px" }} variant='contained' onClick={CloseModal}>ng</Button>
                  <Button style={{ float: "right", marginLeft: "10px" }} variant='contained' onClick={cancelOrder}>Hy n</Button>
                  <Button style={{ float: "right", marginLeft: "10px" }} variant='contained' onClick={PaymentOrder}>Thanh ton</Button>
                  <Button style={{ float: "right" }} variant='contained' onClick={() => updateOrder(detailData.id)}>Sa</Button>

                </>
                :


                detailData.status === "payment" ?
                  <>
                    <Button style={{ float: "right", marginLeft: "10px" }} variant='contained' onClick={CloseModal}>ng</Button>
                    <Button style={{ float: "right" }} variant='contained' onClick={doneOrder}>Tr phng</Button>
                  </>
                  :
                  detailData.status === "done" ?
                    <>
                      <Button style={{ float: "right", marginLeft: "10px" }} variant='contained' onClick={CloseModal}>ng</Button>
                      <Button style={{ float: "right" }} variant='contained' onClick={rePrintForm}>In li ha n</Button>
                    </>
                    : 
                    null
            }
          </Typography>
        </Box>
      </Modal>
    </div>
  );
}
