import React, { useState, useEffect } from 'react';
import { Button, Col, Input, Row } from 'reactstrap';
import { Controller, useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import BookingService from '../../../Shared/Services';
import { AuthService, ModalNoti } from '../../../../../Shared';
import { FormControlLabel, Radio, RadioGroup } from '@mui/material';

const schema = yup
    .object({
        fullname: yup
            .string()
            .min(2, "Ti thiu 2 ch ci")
            .max(50, "Ti a 50 ch ci")
            .required("H Tn khng c b trng")
        ,
        numberPhone: yup
            .string()
            .required("S in thoi khng c b trng")
            .matches(
                /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
                "S in thoi khng ng  dng"
            )
        ,
        cccd: yup
            .number()
            .required("Cn cc cng dn khng c b trng")
            .test('len', 'Sai nh dng cmt hoc cccd', cccd => cccd.toString().length == 10 || cccd.toString().length == 12)
        ,
        birthday: yup
            .date()
            .max(new Date(Date.now() - 567648000000), "Bn cha  18 tui")
            .required("Ngy sinh khng c b trng")
        ,

        startDate: yup.date()
            .min(new Date(Date.now() - 86400000), "Ngy nhn phng khng nh hn ngy hin ti")
            .required("Ngy nhn phng khng c b trng"),
        endDate: yup.date().min(
            yup.ref('startDate'),
            "Ngy kt thc khng th nh hn ngy bt u")
            .required("Ngy tr phng khng c b trng"),
        payment: yup.string()
            .required("Phng thc thanh ton khng c b trng"),

    })
    .required();

function BookingRoom(props) {

    const {
        register,
        handleSubmit,
        reset,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
    });

    const dataSession = JSON.parse(sessionStorage.getItem('user'))



    const [message, setMassage] = useState('')


    useEffect(() => {
        if (dataSession?.customer) {
            reset({
                fullname: '123456',
                numberPhone: '097654321',
                cccd: '09876543212',
                birthday: '2000-11-28',
            })
         
        }

    }, [])


    const sendMail = (data, id) => {
        const param = {
            name: data.fullname,
            to: data.email,
            title: `BN  T PHNG THNH CNG`,
            content: `Cm n qu khch  t phng ti h thng ca chng ti,
                        M n hng ca qu khch l: ${id} `
        }
        AuthService.Send_Mail_To_My_Gmail(param).then(
            res => {
                console.log('Gi mail thnh cng')
            }
        ).catch(err => {
            console.log(err);
        })

    }


    const submitClick = (data) => {

        if (dataSession?.customer) {
            const amount = (data.endDate.valueOf() - data.startDate.valueOf()) / 86400000;
            const params = {
                customer_id: dataSession?.customer.id,
                itemParams: {
                    room_id: props.room.id,
                    start: data.startDate.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                    end: data.endDate.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                    amount: (amount === 0 ? 1 : amount)
                }
            }

            BookingService.Create_Order(params).then(res => {

                if (data.payment === 'paypal') {
                    const param = {
                        status: 'payment'
                    }
                    BookingService.Update_Order(res.data.id, param).then(res => {
                        setMassage(`t phng thnh cng`)
                    }).catch(err => {
                        setMassage('C li xy ra!!!')
                    })
                }
                else {
                    setMassage(`t phng thnh cng`)
                }
            }).catch(err => {
                setMassage('C li xy ra!!!')
            })
        }
        else {
            const params = {
                name: data.fullname,
                role: "customer",
                birthday: data.birthday.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                cccd: data.cccd.toString(),
                phone: data.numberPhone
            }
            BookingService.Create_Cumstomer(params).then(res => {
                const amount = (data.endDate.valueOf() - data.startDate.valueOf()) / 86400000;
                const params = {
                    customer_id: res.data?.id,
                    itemParams: {
                        room_id: props.room.id,
                        start: data.startDate.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                        end: data.endDate.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                        amount: (amount === 0 ? 1 : amount)
                    }
                }
                BookingService.Create_Order(params).then(res => {
                    if (data.payment === 'paypal') {
                        const param = {
                            status: 'payment'
                        }
                        BookingService.Update_Order(res.data.id, param).then(res => {
                            setMassage(`t phng thnh cng, m n l ${res.data.id} `)
                            // sendMail(data, res.data.id)
                        }).catch(err => {
                            setMassage('C li xy ra!!!')
                        })
                    }
                    else {
                        // sendMail(data, res.data.id)
                        setMassage(`t phng thnh cng, m n l ${res.data.id} `)
                    }
                }).catch(err => {
                    setMassage('C li xy ra!!!')
                })
            }).catch(err => {
                setMassage('C li xy ra!!!')
                console.log(err);
            })
        }


    }

    return (
        <Row className='Info-booking'>
            <p><b><h3 className='Info-booking-name middle upper'>Thng tin lin h</h3></b></p>
            <form onSubmit={handleSubmit(submitClick)}>
                {!dataSession?.customer ?
                    <>
                        <Row>
                            <Col xs={6}>
                                <Controller
                                    control={control}
                                    name="fullname"

                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <h6>H v Tn</h6>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                placeholder="H v Tn"
                                                {...field}

                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                            <Col >
                                <Controller
                                    control={control}
                                    name="birthday"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <h6>Ngy sinh</h6>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                {...field}
                                                type='date'
                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={6}>
                                <Controller
                                    control={control}

                                    name="numberPhone"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <h6>S in thoi</h6>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                placeholder="0987654321"
                                                {...field}

                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                            <Col>
                                <Controller
                                    control={control}
                                    name="cccd"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <h6>Cn cc cng dn ( Chng minh th )</h6>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                placeholder="022200001927"
                                                {...field}
                                                type='number'
                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                        </Row>
                    </>
                    :
                    null
                }

                <Row>
                    <Col xs={6}>
                        <Controller
                            control={control}
                            name="startDate"
                            render={({
                                field: { ref, ...field },
                                fieldState: { error },
                            }) => (
                                <>
                                    <h6>Ngy nhn phng</h6>
                                    <Input
                                        className="form-control"
                                        inputRef={ref}
                                        {...field}
                                        type='date'
                                    />
                                    {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                </>
                            )}
                        />
                    </Col>
                {/* </Row>
                <Row> */}
                    <Col>
                        <Controller
                            control={control}
                            name="endDate"
                            render={({
                                field: { ref, ...field },
                                fieldState: { error },
                            }) => (
                                <>
                                    <h6>Ngy tr phng</h6>
                                    <Input
                                        className="form-control"
                                        inputRef={ref}
                                        {...field}
                                        type='date'
                                    />
                                    {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                </>
                            )}
                        />
                    </Col>
                </Row>
                <Row >
                    <h6>Phng thc thanh ton</h6>
                    <Col >
                        <Controller
                            control={control}
                            name="payment"
                            defaultValue={'default'}
                            render={({
                                field: { ref, ...field },
                                fieldState: { error },
                            }) => (
                                <RadioGroup row aria-label="role" {...field}>
                                    <FormControlLabel
                                        value="default"
                                        control={<Radio />}
                                        label="Thanh ton ti quy"
                                    />
                                    <FormControlLabel
                                        value="paypal"
                                        control={<Radio />}
                                        label="Thanh ton ngay"
                                    />
                                </RadioGroup>
                            )}
                        />
                    </Col>

                </Row>

                <Row className='d-flex justify-content-center'>
                    <Col className='d-flex justify-content-center' xs='4'>
                        <button className='button-main' type='submit' >t phng</button>
                    </Col>

                </Row>
            </form>
            <ModalNoti
                message={message}
                done={() => setMassage('')}
            ></ModalNoti>
        </Row>
    );

}

export default BookingRoom