import React, { useState, useEffect } from "react";
import { Controller, useForm } from "react-hook-form";
import { Row, Col, Input } from 'reactstrap';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Button, FormControlLabel, Radio, RadioGroup, Typography } from "@mui/material";
import CustomerService from "../../Shared/Service";
import { ModalNoti } from "../../../../Shared";
import { goTo } from "../../../../Constances/Util";
import ArrowBackIosRoundedIcon from '@mui/icons-material/ArrowBackIosRounded';
import { SetDefaultValueBirthday } from "../../../../Constances/const";

const schema = yup
    .object({
        fullname: yup
            .string()
            .min(2, "Ti thiu 2 k t")
            .max(50, "Ti a 50 k t")
            .required("H Tn khng c b trng")
        ,
        numberPhone: yup
            .string()
            .required("S in thoi khng c b trng")
            .test('len', 'S in thoi khng ng  dng', cccd => cccd?.toString().length == 10)
            .matches(
                /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
                "S in thoi khng ng  dng"
            )
        ,
        cccd: yup
            .string()
            .required("Cn cc cng dn khng c b trng")
            .test('len', 'Sai nh dng cmt hoc cccd', cccd => cccd?.toString().length == 10 || cccd?.toString().length == 12)
        ,
        birthday: yup
            .date()
            .max(new Date(Date.now() - 567648000000), "Bn cha  18 tui")
            .required("Ngy sinh khng c b trng")
        ,
        username: yup.string()
            .min(6, "Ti thiu 6 k t")
            .max(30, "Ti a 30 k t")
            .required("Tn ng nhp khng c b trng")
        ,
        password: yup.string()
            .min(6, "Ti thiu 6 k t")
            .max(30, "Ti a 30 k t")
            .required("Tn ng nhp khng c b trng")
        ,
        role: yup.string()
            .required("Tn ng nhp khng c b trng")
    })
    .required();

const CreateUpdate = (props) => {

    const {
        register,
        reset,
        handleSubmit,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
    });

    const [message, setMassage] = useState('');
    const [signUpStatus, setStatus] = useState(false);
    const [customer, setCus] = useState({})
    const [statusUpdate, setStatusUpdate] = useState(false);
    const [checkUpdate, setCheckUpdate] = useState(false);

    useEffect(() => {
        const id = props.match.params.id
        if (id) {
            setStatusUpdate(true)
            CustomerService.Get_Id_Cus(id).then(res => {
                setCus(res.data)
                const data = res.data
                reset({
                    fullname: data.name,
                    numberPhone: data.phone,
                    username: '111111',
                    password: '123456',
                    cccd: data.cccd,
                    birthday: data.birthday,
                    role: data.role
                })

            }).catch(err => console.log(err))
        }
    }, [])

    const submitClick = (data) => {
        setCheckUpdate(!checkUpdate);

        if (!statusUpdate) {
            const customer = {
                name: data.fullname,
                username: data.username,
                role: data.role,
                birthday: data.birthday.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                cccd: data.cccd.toString(),
                phone: data.numberPhone,
            }
            CustomerService.Create_Cus(customer).then(res => {
                const setPassword = {
                    customer_id: res.data.id,
                    password: data.password
                }
                CustomerService.Set_Password(setPassword).then(res => {
                    setStatus(true)
                    setMassage(`To ti khon thnh cng`)
                }).catch(err => {
                    console.log(err);
                    setMassage('C li xy ra!!!')
                })
            }).catch(err => {
                console.log(err);
                setMassage('C li xy ra!!!')
            })
        }
        else {
            const param = {
                name: data.fullname,
                role: data.role,
                birthday: data.birthday.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                cccd: data.cccd.toString(),
                phone: data.numberPhone,
            }
            CustomerService.Update_Cus(customer.id, param).then(res => {
                setStatus(true)
                setMassage(`Cp nht thng tin thnh cng`)
            }).catch(err => {
                console.log(err);
                setMassage('C li xy ra!!!')
            })
        }
    }

    const checkMessageNoti = () => {
        setMassage('')
        if (signUpStatus) {
            goTo('admin/user')
        }
    }

    return (
        <Row className="CustomerView">
            <Typography sx={{
                cursor: 'pointer',
                color: 'blue',
                ':hover': {
                    textDecorationLine: 'underline'
                }
            }} onClick={() => goTo('admin/customer')}>   <ArrowBackIosRoundedIcon ></ArrowBackIosRoundedIcon> Back</Typography>
            <Col xs={{ size: 6, offset: 3 }} className='form-container'>
                <form onSubmit={handleSubmit(submitClick)}>
                    <Row>
                        <Col>
                            <Controller
                                control={control}
                                name="fullname"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>H v Tn</h6>
                                        <Input
                                            className="form-control"
                                            inputRef={ref}
                                            placeholder="H v Tn"
                                            {...field}
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>
                    {
                        !statusUpdate ? <Row>
                            <Col>
                                <Controller
                                    control={control}
                                    name="username"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <h6>Tn ng nhp</h6>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                {...field}
                                                defaultValue=''
                                                placeholder="username"
                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                        </Row>
                            : null
                    }
                    <Row >
                        <Col >
                            <Controller
                                control={control}
                                name="birthday"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>Ngy sinh</h6>
                                        <Input
                                            className="form-control"
                                            inputRef={ref}

                                            {...field}
                                            type='date'
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                        <Col>
                            <Controller
                                control={control}
                                name="numberPhone"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>S in thoi</h6>
                                        <Input
                                            className="form-control"
                                            inputRef={ref}
                                            {...field}
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>

                    {!statusUpdate ?
                        <>
                            <Row>
                                <Col>
                                    <Controller
                                        control={control}
                                        name="cccd"
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>Cn cc cng dn ( Chng minh th )</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    placeholder="022200001927"
                                                    {...field}
                                                    type='string'
                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                            </Row>
                            <Row >
                                <h6>Vai tr</h6>
                                <Col >
                                    <Controller
                                        control={control}
                                        name="role"
                                        defaultValue={customer.role ? customer.role : 'staff'}
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <RadioGroup row aria-label="role" {...field}>
                                                <FormControlLabel
                                                    value="staff"
                                                    control={<Radio />}
                                                    label="nhn vin"
                                                />
                                            </RadioGroup>
                                        )}
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <Controller
                                        control={control}
                                        name="password"
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>Password</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    {...field}
                                                    defaultValue=''
                                                    type="password"
                                                    placeholder="password"
                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                            </Row>
                        </> : null}

                    <Row className='d-flex justify-content-center'>
                        <Col className='d-flex justify-content-center' xs='4'>
                            <Button className='btn-signin' variant="contained" type='submit' >{!statusUpdate ? 'To ti khon' : 'Cp nht'}</Button>
                        </Col>
                    </Row>
                </form>
            </Col>
            <ModalNoti
                message={message}
                done={() => (checkMessageNoti())}
            ></ModalNoti>
        </Row>
    )
}

export default CreateUpdate;