import React, { useState, useEffect } from "react";
import { Controller, useForm } from "react-hook-form";
import { Row, Col, Input } from 'reactstrap';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Button, Typography } from "@mui/material";
import { ModalNoti } from "../../../../Shared";
import { checkQualityRoom, checkTypeRoom, goTo } from "../../../../Constances/Util";
import ArrowBackIosRoundedIcon from '@mui/icons-material/ArrowBackIosRounded';
import OrderService from '../../shared/Service';

const schema = yup
    .object({
        name: yup
            .string()
            .min(2, "Ti thiu 2 ch ci")
            .max(50, "Ti a 50 ch ci")
            .required("H Tn khng c b trng")
        ,
        numberPhone: yup
            .string()
            .required("S in thoi khng c b trng")
            .matches(
                /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
                "S in thoi khng ng  dng"
            )
        ,
        cccd: yup
            .number()
            .required("Cn cc cng dn khng c b trng")
            .test('len', 'Sai nh dng cmt hoc cccd', cccd => cccd.toString().length == 10 || cccd.toString().length == 12)
        ,
        birthday: yup
            .date()
            .max(new Date(Date.now() - 567648000000), "Bn cha  18 tui")
            .required("Ngy sinh khng c b trng")
        ,

        start: yup.date()
            .min(new Date(Date.now() - 86400000), "Ngy nhn phng khng nh hn ngy hin ti")
            .required("Ngy nhn phng khng c b trng"),
        end: yup.date().min(
            yup.ref('start'),
            "Ngy kt thc khng th nh hn ngy bt u")
            .required("Ngy tr phng khng c b trng")

        ,
        room_id: yup
            .string()
            .required("ID phng khng c b trng")
        ,
    })


const CreateUpdateOrder = (props) => {
    const {
        register,
        reset,
        handleSubmit,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
    });

    const [dataOrder, setDataOrder] = useState({})
    const [message, setMassage] = useState('');
    const [signUpStatus, setStatus] = useState(false);
    const [statusUpdate, setStatusUpdate] = useState(false);
    const [idRoom, setIdRoom] = useState([])

    useEffect(() => {
        const id = props.match.params.id
        if (id) {
            setStatusUpdate(true);
            OrderService.GetOrder(id).then(res => {
                setDataOrder(res.data)
                const data = res.data;
                const items = res.data.items[0];
                
                reset({
                    name: data.name? data.name :'huy',
                    status: data.status,
                    birthday: data.customer.birthday,
                    cccd:data.customer.cccd,
                    numberPhone:data.customer.numberPhone ? data.customer.numberPhone:'0987654321',
                    room_id: items.room_id,
                    start: items.start,
                    end: items.end,
                    amount: items.amount
                })
            }).catch(err => console.log(err))
        }
    }, [statusUpdate])

    useEffect(() => {
        OrderService.Get_List_ID_ROOM("empty").then(res => {
            const result = res.data.filter(el => el.status === 'empty').map(data => {
                return {
                    value: data.id,
                    label: data.name,
                    type:data.type,
                    quality: data.quality
                }
            })
            console.log(result)
            setIdRoom(result)
        })
    }, [])

    const submitClick = (data) => {
        console.log(data)

        if (!statusUpdate) {
            const params = {
                name: data.name,
                role: "customer",
                birthday: data.birthday.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                cccd: data.cccd.toString(),
                phone: data.numberPhone
            }
            OrderService.CreateCustomer(params).then(res => {
                const amount = (data.end.valueOf() - data.start.valueOf()) / 86400000;
                const params = {
                    customer_id: res.data?.id,
                    itemParams: {
                        room_id: data.room_id,
                        start: data.start.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                        end: data.end.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                        amount: amount === 0 ? 1 : amount 
                    }
                }
                OrderService.CreateOrder(params).then(res => {
                     setStatus(true)
                    setMassage(`t phng thnh cng, m n l ${res.data.id} `)
                }).catch(err => {
                    setMassage('C li xy ra!!!')
                })
            }).catch(err => {
                setMassage('C li xy ra!!!')
                console.log(err);
            })
        }
        else {
            const amount = (data.end.valueOf() - data.start.valueOf()) / 86400000;

            const param = {
                status: data.status,
                itemParams: {
                    start: data.start.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                    end: data.end.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
                    amount: amount === 0 ? 1 : amount 
                }
            }
            OrderService.UpdateOrder(dataOrder.id, param).then(res => {
                setStatus(true)
                setMassage(`Cp nht thng tin thnh cng`)
            }).catch(err => {
                console.log(err);
                setMassage('C li xy ra!!!')
            })
        }
    }

    const checkMessageNoti = () => {
        setMassage('')
        if (signUpStatus) {
            goTo('admin/order')
        }
    }

    return (
        <Row className="CustomerView">
            <Typography sx={{
                cursor: 'pointer',
                color: 'blue',
                ':hover': {
                    textDecorationLine: 'underline'
                }
            }} onClick={() => goTo('admin/order')}>   <ArrowBackIosRoundedIcon ></ArrowBackIosRoundedIcon> Back</Typography>
            <Col xs={{ size: 6, offset: 3 }} className='form-container'>
                <form onSubmit={handleSubmit(submitClick)}>
                    {!statusUpdate ?
                        <>
                            <Row>
                                <Col>
                                    <Controller
                                        control={control}
                                        name="name"

                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>H v Tn</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    placeholder="H v Tn"
                                                    {...field}

                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                            </Row>
                            <Row >
                                <Col >
                                    <Controller
                                        control={control}
                                        name="birthday"
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>Ngy sinh</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    {...field}
                                                    type='date'
                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                                <Col>
                                    <Controller
                                        control={control}

                                        name="numberPhone"
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>S in thoi</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    placeholder="0987654321"
                                                    {...field}

                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <Controller
                                        control={control}
                                        name="cccd"
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>Cn cc cng dn ( Chng minh th )</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    placeholder="022200001927"
                                                    {...field}
                                                    type='number'
                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                            </Row>
                            <Row >
                                <Col >
                                    <Controller
                                        control={control}
                                        name="room_id"
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>M phng</h6>
                                                <Input
                                                    placeholder=""
                                                    type="select"
                                                    inputRef={ref}
                                                    {...field}
                                                >
                                                    {idRoom ? idRoom.map(data => {
                                                        return (
                                                            <option value={data.value}>
                                                            {data.label} - {checkTypeRoom(data.type)} - {checkQualityRoom(data.quality)}
                                                            </option>
                                                        );
                                                    }) : ""}
                                                </Input>
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                            </Row>
                        </>
                        : null
                    }
                    <Row >
                        <Col >
                            <Controller
                                control={control}
                                name="start"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>T ngy</h6>
                                        <Input
                                            className="form-control"
                                            // defaultValue={}
                                            inputRef={ref}
                                            {...field}
                                            type='date'
                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                        <Col>
                            <Controller
                                control={control}
                                name="end"
                                render={({
                                    field: { ref, ...field },
                                    fieldState: { error },
                                }) => (
                                    <>
                                        <h6>n ngy</h6>
                                        <Input
                                            className="form-control"
                                            inputRef={ref}
                                            {...field}
                                            type="date"

                                        />
                                        {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                    </>
                                )}
                            />
                        </Col>
                    </Row>

                    <Row className='d-flex justify-content-center'>
                        <Col className='d-flex justify-content-center' xs='4'>
                            <Button className='btn-signin' variant="contained" type='submit' >{!statusUpdate ? 'To n hng' : 'Cp nht'}</Button>
                        </Col>
                    </Row>
                </form>
            </Col>
            <ModalNoti
                message={message}
                done={() => (checkMessageNoti())}
            ></ModalNoti>
        </Row>
    )
}

export default CreateUpdateOrder;