import React, { Component } from 'react';
import { Button, Col, Input, Row } from 'reactstrap';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import EmailIcon from '@mui/icons-material/Email';
import { Controller, useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { ButtonForAll } from "../Button/ButtonForAll";
import AuthService from '../../Services/AuthService';
import ModalNoti from '../ModalNoti/ModalNoti';
const schema = yup
    .object({
        fullname: yup
            .string()
            .required("H tn khng c b trng"),
        numberPhone: yup
            .number()
            .required("S in thoi khng c b trng"),
        email: yup
            .string()
            .required("Email khng c b trng"),
        title: yup
            .string()
            .required("Tiu  sinh khng c b trng"),
        content: yup
            .string()
            .required("Ni dung khng c b trng")
    })
    .required();


function Contact(props) {

    const {
        register,
        handleSubmit,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
    });

    const [message, setMessage] = React.useState('');

    const checkTitle = (type) => {
        switch (type) {
            case 'feedback':
                return 'Th gp  t';
            case 'advertisement':
                return 'Th qung co t';
            case 'enterprise':
                return 'Th mi hp tc t';
            default:
                break;
        }
    }


    const submitClick = (data) => {


        const param = {
            name: data.fullname,
            to: 'lethicham1512@gmail.com',
            title: checkTitle(data.title) + data.email,
            content:`Ni dung l: ${data.content}`
        }

        AuthService.Send_Mail_To_My_Gmail(param).then(
            res => {
                setMessage('Gi mail thnh cng')
            }
        ).catch(err => {
            console.log(err);
            setMessage('Gi mail tht bi')
        })

    }

    const checkMessageNoti = () => {
        setMessage('')
    }

    return (
        <Row className='contact'>
            <Col className='contact-hot'>
                <p className='contact-hot-title'>CHNG TI</p>
                <p className='contact-hot-content'> </p>
            </Col>
            <Col className='contact-main' xs={9}>
                <p className='contact-main-title'>PHN HI</p>
                <p className='contact-main-form'>
                    <form onSubmit={handleSubmit(submitClick)}>
                        <Row>
                            <Col xs={6}>
                                <Controller
                                    control={control}
                                    name="fullname"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                placeholder="H v Tn"
                                                {...field}

                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                            <Col >
                                <Controller
                                    control={control}
                                    name="numberPhone"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                placeholder="S in thoi: 09........"
                                                {...field}


                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs={6}>
                                <Controller
                                    control={control}
                                    name="email"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                placeholder="you@gmail.com"
                                                {...field}


                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                            <Col>
                                <Controller
                                    control={control}
                                    name="title"
                                    defaultValue={'feedback'}
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                placeholder="H v Tn"
                                                {...field}
                                                type='select'
                                            >
                                                <option value={'feedback'}>Gp </option>
                                                <option value={'advertisement'}>Qung co</option>
                                                <option value={'enterprise'}>Doanh nghip</option>
                                            </Input>
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <Controller
                                    control={control}
                                    name="content"
                                    render={({
                                        field: { ref, ...field },
                                        fieldState: { error },
                                    }) => (
                                        <>
                                            <Input
                                                className="form-control"
                                                inputRef={ref}
                                                style={{
                                                    minHeight: '150px',
                                                    maxHeight: '150px'
                                                }}
                                                placeholder="Ni dung"
                                                {...field}
                                                type='textarea'
                                            />
                                            {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                        </>
                                    )}
                                />
                            </Col>
                        </Row>
                        <Row className='d-flex justify-content-center'>
                            <Col className='d-flex justify-content-center' xs={2}>
                                <button className='button-main' type='submit'>Gi</button>

                            </Col>

                        </Row>

                    </form>
                    <div className="info-company">
                        <p className="fontsz-25"><b>Cng ty c phn du lch Vit Nam</b></p>
                        <p><b>a ch</b></p>
                        <p><LocationOnIcon></LocationOnIcon> <span>Tng 11, Ta Peakview, 36 Hong Cu, ng a.</span></p>
                        <p><LocalPhoneIcon></LocalPhoneIcon> <span>in thoi: 024 7109 9999.</span></p>
                        <p><EmailIcon></EmailIcon> <span>Email: info@mytour.vn</span></p>
                    </div>
                </p>
            </Col>
            <ModalNoti
                message={message}
                done={() => (checkMessageNoti())}
            ></ModalNoti>
        </Row>
    );

}

export default Contact