import React, { useState } from "react";
import { Form as FormRender, Input, Button, Row, Col } from "reactstrap";
import { Controller, useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import ModalNoti from "../ModalNoti/ModalNoti";
import SignUpService from "../../Services/SigupService";
import { goTo } from "../../../Constances/Util";

const schema = yup
  .object({
    fullname: yup
      .string()
      .min(2, "Ti thiu 2 k t")
      .max(50, "Ti a 50 k t")
      .required("H Tn khng c b trng")
    ,
    numberPhone: yup
      .string()
      .required("S in thoi khng c b trng")
      .matches(
        /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
        "S in thoi khng ng  dng"
      )
    ,
    cccd: yup
      .string()
      .required("Cn cc cng dn khng c b trng")
      .test('len', 'Sai nh dng cmt hoc cccd', cccd =>cccd.toString().length == 10 || cccd.toString().length == 12)
    ,
    birthday: yup
      .date()
      .max(new Date(Date.now() - 567648000000), "Bn cha  18 tui")
      .required("Ngy sinh khng c b trng")
    ,
    username: yup.string()
      .min(6, "Ti thiu 6 k t")
      .max(30, "Ti a 30 k t")
      .required("Tn ng nhp khng c b trng")
    ,
    password: yup.string()
      .min(6, "Ti thiu 8 k t")
      .max(30, "Ti a 30 k t")
      .required("Tn ng nhp khng c b trng")
    ,


  })
  .required();

function SigupForm(props) {
  const {
    register,
    reset,
    handleSubmit,
    formState: { errors },
    control,
  } = useForm({
    resolver: yupResolver(schema),
  });

  const [message, setMassage] = useState('');
  const [signUpStatus, setStatus] = useState(false);

  const submitClick = (data) => {
    const customer = {
      name: data.fullname,
      username: data.username,
      role: "customer",
      birthday: data.birthday.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
      cccd: data.cccd.toString(),
      phone: data.numberPhone,
   
    }
    console.log(customer);
    SignUpService.Create_Customer(customer).then(res => {
      const setPassword = {
        customer_id: res.data.id,
        password: data.password
      }
      SignUpService.Set_Password(setPassword).then(res => {
        setStatus(true)
        setMassage(`To ti khon thnh cng`)
      }).catch(err => {
        console.log(err);
        setMassage('C li xy ra!!!')
      })
    }).catch(err => {
      console.log(err);
      setMassage('C li xy ra!!!')
    })

  }

  const checkMessageNoti = () => {
    setMassage('')
    if (signUpStatus) {
      goTo('app/login')
    }
  }

  return (
    <div className="container-sigin">
      <Row>
        <Col xs={{ size: 6, offset: 3 }} className='form-container'>
        <h3 className="title">ng k</h3>
          <form onSubmit={handleSubmit(submitClick)}>
            <Row>
              <Col xs={6}>
                <Controller
                  control={control}
                  name="fullname"
                  defaultValue={''}
                  render={({
                    field: { ref, ...field },
                    fieldState: { error },
                  }) => (
                    <>
                      <h6>H v Tn</h6>
                      <Input
                        className="form-control"
                        inputRef={ref}
                        placeholder="H v Tn"
                        {...field}

                      />
                      {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                    </>
                  )}
                />
              </Col>
              <Col>
                <Controller
                  control={control}
                  name="username"
                  render={({
                    field: { ref, ...field },
                    fieldState: { error },
                  }) => (
                    <>
                      <h6>Tn ng nhp</h6>
                      <Input
                        className="form-control"
                        inputRef={ref}
                        {...field}
                        defaultValue=''
                        placeholder="username"
                      />
                      {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                    </>
                  )}
                />
              </Col>
            </Row>
            <Row >
              <Col >
                <Controller
                  control={control}
                  name="birthday"
                  render={({
                    field: { ref, ...field },
                    fieldState: { error },
                  }) => (
                    <>
                      <h6>Ngy sinh</h6>
                      <Input
                        className="form-control"
                        inputRef={ref}
                        {...field}
                        type='date'
                      />
                      {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                    </>
                  )}
                />
              </Col>
              <Col>
                <Controller
                  control={control}
                  defaultValue={''}
                  name="numberPhone"
                  render={({
                    field: { ref, ...field },
                    fieldState: { error },
                  }) => (
                    <>
                      <h6>S in thoi</h6>
                      <Input
                        className="form-control"
                        inputRef={ref}
                        placeholder="0987654321"
                        {...field}

                      />
                      {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                    </>
                  )}
                />
              </Col>
            </Row>
            <Row>
              <Col xs={6}>
                <Controller
                  control={control}
                  name="cccd"
                  render={({
                    field: { ref, ...field },
                    fieldState: { error },
                  }) => (
                    <>
                      <h6>Cn cc cng dn ( Chng minh th )</h6>
                      <Input
                        className="form-control"
                        inputRef={ref}
                        placeholder="022200001927"
                        {...field}
                        type='string'
                      />
                      {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                    </>
                  )}
                />
              </Col>
              <Col>
                <Controller
                  control={control}
                  name="password"
                  render={({
                    field: { ref, ...field },
                    fieldState: { error },
                  }) => (
                    <>
                      <h6>Password</h6>
                      <Input
                        className="form-control"
                        inputRef={ref}
                        {...field}
                        defaultValue=''
                        type="password"
                        placeholder="password"
                      />
                      {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                    </>
                  )}
                />
              </Col>
            </Row>

            <Row className='d-flex justify-content-center'>
              <Col className='d-flex justify-content-center' xs='4'>
                <button className='button-main' type='submit' >ng k</button>
              </Col>

            </Row>
          </form>
          <ModalNoti
            message={message}
            done={() => (checkMessageNoti())}
          ></ModalNoti>
        </Col>
      </Row>
    </div>
  );
}


export default SigupForm;
