import { Line, Bar } from 'react-chartjs-2';
import { 
  Chart as ChartJS, 
  CategoryScale, 
  BarController, 
  LinearScale, 
  BarElement, 
  LineElement,
  PointElement,
  Title,
  Tooltip,
  Legend} 
from "chart.js";
import { ConvertChartData} from './ProcessData';

ChartJS.register(CategoryScale, BarController, LinearScale, BarElement, LineElement, PointElement, Title, Tooltip, Legend);


export const BarChart = (props) => {
  const { data, type } = props; 
  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: "THNG K",
      },
      tooltip: {
        callbacks: {
          title: function (type) {
            return type === "total" ? "Tng tin: " : "Lng t phng: ";
          }
        }
      },
    },
  };
  if (type === "total") {
    options.indexAxis = "y"
  }
  const labels = ["Th 2", "Th 3", "Th 4", "Th 5", "Th 6", "Th 7", "CN"];
  const dataChart = {
    labels,
    datasets: ConvertChartData(data, type)
  };
  return <Bar options={options} data={dataChart} />;
}
