import React, { useState } from 'react';
import { Button, Col, Input, Row } from "reactstrap";
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { Controller, useForm } from 'react-hook-form';
import MyProfileService from "../../shared/Service";
import { ModalNoti } from '../../../../Shared';
import { LOCALSTORAGE } from '../../../../Constances/const';

const schema = yup
    .object({
        fullname: yup
            .string()
            .min(2, "Ti thiu 2 k t")
            .max(50, "Ti a 50 k t")
            .required("H Tn khng c b trng")
        ,
        numberPhone: yup
            .string()
            .required("S in thoi khng c b trng")
            .matches(
                /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
                "S in thoi khng ng  dng"
            )
        ,
        cccd: yup
            .number()
            .required("Cn cc cng dn khng c b trng")
            .test('len', 'Sai nh dng cmt hoc cccd', (cccd) => cccd?.toString().length !== 12 || cccd?.toString().length !== 10)

        ,
        birthday: yup
            .date()
            .max(new Date(Date.now() - 567648000000), "Bn cha  18 tui")
            .required("Ngy sinh khng c b trng")
        ,

    })
    .required();

export default function Profile(props) {

    const profile = props.profile;

    const [message, setMassage] = useState('');

    const [showEdit, setShow] = useState(false);

    const {
        register,
        reset,
        handleSubmit,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
    });

    const submitClick = (data) => {
        console.log(data.birthday.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10));
        const param = {
            name: data.fullname,
            birthday: data.birthday.toLocaleString('en-CA', { timeZone: 'UTC' }).slice(0, 10),
            cccd: data.cccd.toString(),
            phone: data.numberPhone,
        }

        MyProfileService.Update_Customer(profile.id, param).then(res => {
            setMassage('Update thnh cng')
        }).catch(res => {
            console.log(res);
            setMassage('C li xy ra!!!!')
        })
    }

    return (
        <Row className='Profile'>

            {
                showEdit ?
                    <>
                        <form onSubmit={handleSubmit(submitClick)}>
                            <Row>
                                <Col xs={6}>
                                    <Controller
                                        control={control}
                                        name="fullname"
                                        defaultValue={profile.name}
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>H v Tn</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    placeholder="H v Tn"
                                                    {...field}

                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                                <Col >
                                    <Controller
                                        control={control}
                                        name="birthday"
                                        defaultValue={profile.birthday}
                                        render={({
                                            field: { ref, ...field },
                                            fieldState: { error },
                                        }) => (
                                            <>
                                                <h6>Ngy sinh</h6>
                                                <Input
                                                    className="form-control"
                                                    inputRef={ref}
                                                    {...field}
                                                    type='date'
                                                />
                                                {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                            </>
                                        )}
                                    />
                                </Col>
                                    <Col xs={6}>
                                        <Controller
                                            control={control}
                                            defaultValue={profile.phone}
                                            name="numberPhone"
                                            render={({
                                                field: { ref, ...field },
                                                fieldState: { error },
                                            }) => (
                                                <>
                                                    <h6>S in thoi</h6>
                                                    <Input
                                                        className="form-control"
                                                        inputRef={ref}
                                                        placeholder="0987654321"
                                                        {...field}

                                                    />
                                                    {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                                </>
                                            )}
                                        />
                                    </Col>
                                    <Col>
                                        <Controller
                                            control={control}
                                            name="cccd"
                                            defaultValue={profile.cccd}
                                            render={({
                                                field: { ref, ...field },
                                                fieldState: { error },
                                            }) => (
                                                <>
                                                    <h6>Cn cc cng dn ( Chng minh th )</h6>
                                                    <Input
                                                        className="form-control"
                                                        inputRef={ref}
                                                        placeholder="022200001927"
                                                        {...field}
                                                        type='number'
                                                    />
                                                    {Boolean(error) ? <p className='error_text'>{error.message}</p> : <p className='error_text'> </p>}
                                                </>
                                            )}
                                        />
                                    </Col>
                                </Row>
                            <Row className='d-flex justify-content-center'>
                                <Col className='d-flex justify-content-around' xs='4'>
                                    <Col xs={6}>
                                    <button className='button-main'  type='submit' >Cp nht</button>
                                    </Col>
                                    <button className='button-cancel' onClick={() => { setShow(false)}} color='secondary' >Hu</button>
                                </Col>
                            </Row>
                        </form>

                    </>
                    :
                    <>
                        <p className='name'>H tn : <h5> {profile.name}</h5></p>
                        <p>Tn ng nhp : {profile.username}</p>
                        <p>Vai tr : {profile.role}</p>
                        <p>cn cc cng dn : {profile.cccd}</p>
                        <p>Ngy sinh : {profile.birthday}</p>
                        <p>S in thoi : {profile.phone}</p>
                        <Col xs={2}>
                            <button className='button-main' onClick={() => { setShow(true) }}>chnh sa thng tin</button>
                        </Col>
                    </>
            }

            <ModalNoti
                message={message}
                done={() => (setMassage(''), setShow(false))}
            ></ModalNoti>
        </Row>
    );
}